				</el-form-item>
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' v-else class="input" label="团队名称" prop="tuanduimingcheng">
					<el-input v-model="ruleForm.tuanduimingcheng" placeholder="团队名称" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' class="upload" v-if="type!='info' && !ro.tuanduifengmian" label="团队封面" prop="tuanduifengmian">
					<file-upload
						tip="点击上传团队封面"
						action="file/upload"
